___CSS_LOADER_EXPORT___.push([module.id, `body {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100dvh;
  background-color: #282828;
  color: #ffffff;
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    sans-serif;
}

header {
  position: relative;
  display: flex;
  justify-content: flex-end;
  background-color: #161616;
  padding: 12px;
}

main {
  padding: 20px;
}

.dropdown {
  position: relative;
  background-color: rgb(62, 62, 141);
  border-radius: 6px;
  width: 100px;
  text-align: center;
  padding: 6px;
}

.dropdown:hover {
  cursor: pointer;
  background-color: rgb(51, 51, 124);
}

.dropdown-items {
  display: flex;
  flex-direction: column;
  gap: 8px;
  position: absolute;
  background-color: rgb(62, 62, 141);
  padding: 8px;
  margin-top: 10px;
  border-radius: 6px;
  min-width: 100%;
  left: 0;
}

.dropdown-items a {
  color: #ffffff;
}

.is-hidden {
  display: none;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.carrousel {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
}

.carrousel > button {
  background-color: #161616;
  border: 1px solid #747474;
  color: #fff;
}

.carrousel > button:hover {
  opacity: 0.8;
}

.carrousel-indicators {
  display: flex;
  justify-content: center;
  grid-column: 1 / 4;
  gap: 10px;
}

.carrousel-indicators span {
  padding: 10px;
  border-radius: 999px;
  border: 1px solid #b6b6b6;
}

.is-active {
  background-color: #747474;
}

.carrousel-images {
  border: 1px solid #747474;
}

.carrousel-images img {
  display: none;
}

.is-selected {
  display: block !important;
}
